"use client";

import { FileText } from "lucide-react";
import { useLatex } from "@/lib/LatexContext";
import ReactKatex from '@pkasila/react-katex';
import 'katex/dist/katex.min.css';
import { LaTeX } from 'latex2react';



export default function Pdf() {
  const { latexContent } = useLatex();
const content = String.raw`
Here is some great equation:

$$x = \frac{{-b \pm \sqrt{b^2-4ac}}}{{2a}}$$

And now for a great diagram:

\begin{pspicture}(0,-3)(8,3)
\rput(0,0){$x(t)$}
\rput(4,1.5){$f(t)$}
\rput(4,-1.5){$g(t)$}
\rput(8.2,0){$y(t)$}
\rput(1.5,-2){$h(t)$}
\psframe(1,-2.5)(7,2.5)
\psframe(3,1)(5,2)
\psframe(3,-1)(5,-2)
\rput(4,0){$X_k = \frac{1}{p} \sum \limits_{n=\langle p\rangle}x(n)e^{-ik\omega_0n}$}
\psline{->}(0.5,0)(1.5,0)
\psline{->}(1.5,1.5)(3,1.5)
\psline{->}(1.5,-1.5)(3,-1.5)
\psline{->}(6.5,1.5)(6.5,0.25)
\psline{->}(6.5,-1.5)(6.5,-0.25)
\psline{->}(6.75,0)(7.75,0)
\psline(1.5,-1.5)(1.5,1.5)
\psline(5,1.5)(6.5,1.5)
\psline(5,-1.5)(6.5,-1.5)
\psline(6,-1.5)(6.5,-1.5)
\pscircle(6.5,0){0.25}
\psline(6.25,0)(6.75,0)
\psline(6.5,0.5)(6.5,-0.5)
\end{pspicture}
`;

  return (
    <>
    
      {
        !latexContent ?
      <div className="p-4">
        
        {/* {latexContent} */}
    <LaTeX content={content} />

      </div> 
      :
      <div className="bg-slate-200 h-full p-4">
        <div className="border-2 border-gray-300 h-full rounded-xl overflow-hidden ">
          <div className="flex flex-col justify-center items-center h-full text-slate-500">
            <FileText className="h-10 w-10" />
            <p className="text-2xl"> pdf here</p>
          </div>
        </div>
      </div>
      
      }

    </>
  )
}
